@import '../../sass';

.select {
    display: flex;
    flex-direction: column;
    @media ($desktop) {
        flex-direction: row;
    }
    &__location {
        position: relative;
    }
    &__header {
        @include regular15();
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 23.2rem;
        height: 4rem;
        background-color: $white;
        border: 1px solid #D2E5F9;
        box-sizing: border-box;
        border-radius: 3px;
        margin-right: 24px;
        padding: 0 16px;
        @media ($phone) {
            width: 36.2rem !important;
        }
        @media ($desktopMax) {
            width: 44.3rem !important;
        }
        &-status {
            width: 23.2rem;
            margin-right: 0 !important;
            margin-top: 10px;
            @media ($phone) {
                width: 36.2rem !important;
            }
            @media ($desktop) {
                margin-top: 0;
            }
        }
        &-visible {
            //border-bottom: 1px solid white;
            border-radius: 3px 3px 0 0;
        }
    }
    &__content {
        display: flex;
    }
    &__pipe {
        width: 1px;
        height: 20px;
        color: #88B1DD;
        margin: 0 16px;
    }
    &__arrow {
        display: inline-flex;
        width: 8px;
        height: 8px;
        border-top: 1px solid #88B1DD;
        border-left: 1px solid #88B1DD;
        transform: rotate(225deg);
        cursor: pointer;
        //margin-top: 1.5px;
        &-show {
            transform: rotate(45deg) !important;
        }
    }
    &__actions {
        //position: absolute;
        //left: 0;
        //bottom: -251px;
        width: 232px;
        height: 252px;
        background-color: $white;
        border: 1px solid #D2E5F9;
        border-top: none;
        box-sizing: border-box;
        z-index: 1000;
        overflow: hidden;
        @media ($phone) {
            width: 362px;
        }
        @media ($desktop) {
            width: 443px;
        }
        &-status {
            width: 232px;
            height: auto;
            bottom: -145px;
            @media ($phone) {
                width: 362px;
            }
        }
    }
    &__search {
        position: relative;
        display: flex;
        align-items: center;
        width: 200px;
        height: 40px;
        border: 1px solid #D2E5F9;
        box-sizing: border-box;
        border-radius: 2px;
        margin: 16px;
        @media ($phone) {
            width: 330px;
        }
        @media ($desktopMax) {
            width: 411px;
        }
        &-border-filled {
            border-color: #88B1DD;
        }
        &-border-focus {
            border-color: #0075F6;
        }
        &-border-invalid {
            border-color: #EB5757;
        }
    }
    &__icon {
        margin: 0 11px 0 14.5px;
    }
    &__input {
        @include regular15();
        width: 150px;
        border: none;
        @media ($phone) {
            width: 250px;
        }
        @media ($desktopMax) {
            width: 348px;
        }
        &::placeholder {
            color: #88B1DD;
            border: none;
            outline: none;
        }
    }
    &__error {
        @include medium12();
        position: absolute;
        left: 0;
        bottom: -16px;
        color: #EB5757;
    }
    &__list {
        max-height: 180px;
        overflow-y: scroll;
    }
    &__location-choice {
        @include regular15();
        display: flex;
        align-items: center;
        height: 36px;
        padding-left: 21px;
        cursor: pointer;
        &:hover {
            background-color: #E7F2FE;
        }
    }
    &__name {
        margin-left: 8px;
        &-checked {
            color: #0075F6;
        }
    }
    &__info {
        color: #88B1DD;
    }
}